class ContactIntrestedEnquiryComponent {
    constructor(authService, router, dialogRef, enqDataRecevied) {
        this.authService = authService;
        this.router = router;
        this.dialogRef = dialogRef;
        this.enqDataRecevied = enqDataRecevied;
        this.enquiryData = enqDataRecevied;
    }
    ngOnInit() {
    }
    onNoClick() {
        this.dialogRef.close();
    }
    saveResponse() {
        this.authService.updateEnquiry(this.enquiryData).subscribe((response) => {
            console.log(' Demo  Added:: ' + response);
            this.dialogRef.close();
            this.router.navigate(['/learnAdmin/manageEnq']);
        });
    }
}


